
body {
  background-color: $registration-bg-color;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
}

body.modal-open {
  overflow: hidden;
}

ul,
h1,
h2,
h3,
h4,
h5,
p {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;

}
// заголовок секций для моб и таблет

.section-title {
  font-family: Caveat Brush;
  font-size: 24px;
  line-height: 1.65;
  text-align: center;
  color: $primary-text-color;

  @include for-size(tablet-up) {
    font-size: 36px;
    line-height: 1.81;
    text-align: center;
  }
}


.button {
 
  width: 145px;
  padding: 0;
  margin: 0;
  margin-right: auto;
  margin-left: auto;
  font-family: Caveat Brush;
  font-size: 18px;
  line-height: 2.25;

  cursor: pointer;
  border: none;
  color: $title-button-color;
  background-color: $bg-button-color;
  border-radius: 25px;


  &:focus,
  &:hover {
    background-color: $title-button-color;
    color: $bg-button-color;

    -webkit-box-shadow: 0 14px 28px rgba(171, 129, 108, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22),
      inset 3px 0px 30px 8px rgb(171 129 108 / 25%);

            box-shadow: 0 14px 28px rgba(171, 129, 108, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22),
      inset 3px 0px 30px 8px rgb(171 129 108 / 25%);
  }

  &:focus,
  &:hover {
 
    background-color: $title-button-color;
    color: $bg-button-color;
 
    -webkit-box-shadow: 0 14px 28px rgba(171, 129, 108, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22),
      inset 3px 0px 30px 8px rgb(171 129 108 / 25%);
 
            box-shadow: 0 14px 28px rgba(171, 129, 108, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22),
      inset 3px 0px 30px 8px rgb(171 129 108 / 25%);
  }



  @include for-size(desktop-up) {
    width: 158px;
    font-size: 24px;
  
  }
}


.link-animation {
  -webkit-transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

// Read more // less
.read-more-btn {
  margin: 0;
  padding: 0;
  font-family: Caveat Brush, sans-serif;
  font-size: 18px;
  line-height: 3.61;
  color: $carousel-color;
  text-align: start;
  border: none;
  background-color: inherit;

  @include for-size(desktop-up) {
    display: none;
  }
}

.read-more-checker {
  opacity: 0;
  position: absolute;

  & ~ .limited {
    display: none;
  }

  &:checked ~ .limited {
    display: inherit;
  }

  & ~ .read-more-btn::before {
    content: 'Read more';
  }

  &:checked ~ .read-more-btn::before {
    content: 'Less';
  }

  @include for-size(desktop-up) {
    display: none;
  }
}