
.program-section {

    @include for-size (phone-plus-up){
     width: 480px;
 
 }
       
    @include for-size (tablet-up){
        position: relative;
        width: 665px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        margin-bottom: 0;
     }
 
     @include for-size (desktop-up){
         width: 854px;
         height: 520px;
         display: -webkit-box;
         display: -ms-flexbox;
         display: flex;
         -webkit-box-pack: justify;
             -ms-flex-pack: justify;
                 justify-content: space-between;
     }
 }
 
 
 .program__title {
     font-family: Caveat Brush;
     font-style: 400;
     font-weight: 400;;
     
     color: $primary-text-color;
     
 
     @include for-size(phone-only){
         font-size: 24px;
         line-height: 2.71;
         text-align: center;
     }
 
     @include for-size(phone-plus-up){
         font-size: 24px;
         line-height: 2.71;
         text-align: center;
     }
 
     @include for-size(tablet-up){
         font-size: 24px;
         line-height: 2.71;
         text-align: start;
     }
     
     @include for-size(desktop-up){
         font-size: 36px;
         line-height: 1.81;
         text-align: start;
     }    
     
     
 }
 .program__limited,
 .program__text {
     display: inline-block;
     color: $primary-text-color;
 
     @include for-size(phone-plus-only){
         margin-top: 10px;
         font-size: 16px;
         line-height: 1.25;
     
     }
 
     @include for-size(tablet-up){
         margin-top: 10px;
         width: 298px;
         font-size: 16px;
         line-height: 1.25;
     }
 
     @include for-size(desktop-up){
         margin-top: 0;
         min-width: 392px;
         font-size: 18px;
         line-height: 1.22;
     }
 }
 
 .desktop-text {
     @include for-size (tablet-only){
         display: none;
     }
 }
 
 
 
 .program__list {
     list-style: none;
     color: $primary-text-color;
 
     & li::before {
         content: ">";
         margin-right: 8px;
     }
 
     @include for-size(tablet-only ) {
         font-size: 16px;
         line-height: 1.25;
     }
 
  
     @include for-size(tablet-up) {
         width: 300px;
         font-size: 16px;
         line-height: 1.25;
     }
 
     @include for-size(desktop-up) {
         width: 450px;
         font-size: 18px;
         line-height: 1.22;
         margin-right: 40px;
     }
 }
 
 .program__tail {
     margin: 0;
     padding: 0;
     margin-top: 10px;
 
     @include for-size (phone-only) {
         font-size: 16px;
         line-height: 1.25;
     }
 
     @include for-size (phone-plus-up) {
         font-size: 16px;
         line-height: 1.25;
     }
 
 
     @include for-size (desktop-up) {
         font-size: 18px;
         line-height: 1.22;
         margin-top: 15px;
 
         &:first-child {
             margin-top: 21px;
         }
           
         &:last-child {
             margin-bottom: 21px;
         }
         
    }
 } 
 
 .program__btn {
     margin: 0;
     padding: 0;
     font-family: Caveat Brush, sans-serif;
     font-size: 18px;
     line-height: 3.61;
     color: $carousel-color;
     text-align: start;
     border: none;
     background-color: inherit;
     
 
     @include for-size(desktop-up) {
         display: none;
     }
 }
 
  
 
 .program__checker {
     opacity: 0;
     position: absolute;
 
     & ~ .program__limited {
        display: none;
      }
 
     
     &:checked ~ .program__limited {
         display: inherit;
     } 
       
     & ~ .program__btn::before {
         content: "Read more";
     }
         
         
     &:checked ~ .program__btn::before {
         content: "Less";
     }
    
    
 
     @include for-size (desktop-up){
         display: none;
     }
 }
 
   .program__container {
     @include for-size (phone-plus-up) {
         display: -webkit-box;
         display: -ms-flexbox;
         display: flex;
         -webkit-box-pack: center;
             -ms-flex-pack: center;
                 justify-content: center;
     }
   }
 
 
 .program__grid {
     position: relative;
     padding: 0;
     margin: 0;
     height: 423px;
     margin-top: 0;
     margin-bottom: 38px;
 
 
 
     @include for-size (tablet-up) {
         position: absolute;
         right: 0px;
         top: 0;
         width: 300px;
 
         margin-bottom: 0;
         margin-top: 0;
     }
 
     @include for-size(desktop-up){
         position: absolute;
         right: 0px;
         width: 392px;
         height: 520px;
    }
 }
 
