

.footer__container {
  @include for-size(phone-plus-up) {
    width: 480px;
}
@include for-size(tablet-up) {
    width: 665px;
}
@include for-size(desktop-up) {
    width: 854px;
}
}

.contact__title {
font-family: Caveat Brush;
font-style: normal;
font-weight: 400;

text-align: center;
margin-top: 77px;
color: $primary-text-color;

@include for-size(phone-plus-only) {
margin-top: 27px;
font-size: 24px;
line-height: 2.71;
}
@include for-size(tablet-up) {
margin-top: 51px;
font-size: 24px;
line-height: 2.71;
}
@include for-size(desktop-up) {
margin-top: 77px;
font-size: 36px;
line-height: 1.81;
}
}

.contact__text {
font-family: Montserrat;
font-style: normal;
font-weight: 400;

text-align: center;

color: $primary-text-color;

@include for-size(phone-plus-only) {
font-size: 16px;
line-height: 4.06;
}
@include for-size(tablet-up) {
font-size: 16px;
line-height: 4.06;
}
@include for-size(desktop-up) {
font-size: 18px;
line-height: 3.61;
}
}

.footer__form {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-orient: vertical;
-webkit-box-direction: normal;
  -ms-flex-direction: column;
      flex-direction: column;
margin: auto;

@include for-size(phone-plus-only) {
max-width: 290px;
}
@include for-size(tablet-up) {
width: 410px;
}
@include for-size(desktop-up) {
width: 430px;
}
}

.footer-form__input {
width: 100%;
height: 22px;
padding: 0;
font-family: Montserrat;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1.25;
color: rgba(46, 37, 32, 1);
background-color: transparent;
border-left: none;
border-right: none;
border-top: none;
border-bottom: 1px solid $input-border-color;
outline: none;

&:first-child {
margin-bottom: 15px;
}

&:invalid:not(:focus) {
border: 1px solid #f00;
}
&::-webkit-input-placeholder {
font-family: Montserrat;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1.25;
color: $placeholder-color;
}
&::-moz-placeholder {
font-family: Montserrat;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1.25;
color: $placeholder-color;
}
&:-ms-input-placeholder {
font-family: Montserrat;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1.25;
color: $placeholder-color;
}
&::-ms-input-placeholder {
font-family: Montserrat;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1.25;
color: $placeholder-color;
}
&::placeholder {
font-family: Montserrat;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1.25;
color: $placeholder-color;
}
}

.footer-button {
display: block;
text-align: center;

@include for-size(phone-plus-only) {
margin-bottom: 47px;
margin-top: 37px;
}
@include for-size(tablet-up) {
margin-bottom: 50px;
margin-top: 27px;
}
@include for-size(desktop-up) {
margin-bottom: 82px;
margin-top: 27px;
}
}

.footer__social {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-pack: center;
  -ms-flex-pack: center;
      justify-content: center;
height: 19px;
margin-bottom: 35px;
}

.footer__item {
display: -webkit-inline-box;
display: -ms-inline-flexbox;
display: inline-flex;
-webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;

&:not(:last-child) {
margin-right: 9px;
}
}

.footer-icons__link:hover,
.footer-icons__link:focus {
fill: $accent-title-btn-color;
}

.footer__icon-twitter {
@include for-size(tablet-only) {
width: 18.95px;
height: 15px;
}
@include for-size(desktop-up) {
height: 19px;
width: 24px;
}
}

.footer__icon-instagram {
@include for-size(tablet-only) {
height: 15px;
width: 15px;
}
@include for-size(desktop-up) {
height: 19px;
width: 19px;
}
}

.footer__icon-facebook {
@include for-size(tablet-only) {
height: 15px;
width: 8.11px;
}
@include for-size(desktop-up) {
width: 10.27px;
height: 19px;
}
}

.footer-nav {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-pack: justify;
  -ms-flex-pack: justify;
      justify-content: space-between;

padding-bottom: 21px;
-webkit-box-align: baseline;
  -ms-flex-align: baseline;
      align-items: baseline;

@include for-size(phone-plus-only) {
-ms-flex-pack: distribute;
    justify-content: space-around;
}
}

.footer__tel,
.footer__location {
font-size: 16px;
line-height: 1.25;
color: $primary-text-color;

&:hover,
&:focus {
 color: $accent-title-btn-color;
fill: $accent-title-btn-color;
}
}